import React, { useState } from 'react';
import * as Yup from 'yup';
import { Formik } from 'formik';
import Model from '../../../components/UI/Model/Model';
import { COMMISSION_PERCENTAGE_REQUIRED } from '../../../constants/errorConstants';

const DriverCommissionModel = props => {
  const [isSubmitted, setIsSubmitted] = useState(false);
  const closeModel = () => {
    const { setModelOpen } = props;
    setModelOpen(false);
  };
  const validationSchema = Yup.object({
    commissionPercentage: Yup.number()
      .typeError('Commission must be a number')
      .label('Commission')
      .min(1)
      .max(100)
      .required(COMMISSION_PERCENTAGE_REQUIRED),
  });
  const submitBtnHandler = values => {
    console.log(values);
    setIsSubmitted(true);
  };

  return (
    <Formik
      initialValues={{
        commissionPercentage: props.commissionPercentage
          ? props.commissionPercentage
          : '',
      }}
      validationSchema={validationSchema}
      onSubmit={(values, { setSubmitting }) => {
        submitBtnHandler(values);
        setSubmitting(false);
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        // isSubmitting,
      }) => (
        <Model
          closeModel={closeModel}
          modalId="modal-delete"
          headerTitle={
            props.isUpdated === true
              ? 'Are you sure you want to update commission?'
              : 'Are you sure you want to accept?'
          }
        >
          <form onSubmit={handleSubmit}>
            <div className="from-group text-left">
              <h4 className="required-field">
                Please enter the mutually agreed commission rate(%):
              </h4>
              <input
                type="text"
                className="form-control"
                name="commissionPercentage"
                value={values.commissionPercentage}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.commissionPercentage &&
                  touched.commissionPercentage &&
                  errors.commissionPercentage}
              </div>
            </div>
            <div className="clearfix m-t-md">
              <button
                type="submit"
                className="btn btn-primary"
                data-toggle="modal"
                data-dismiss="modal"
                data-target="#modal-successb"
                disabled={isSubmitted}
              >
                Submit
              </button>
            </div>
          </form>
        </Model>
      )}
    </Formik>
  );
};

export default DriverCommissionModel;
