import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http';
import { Table } from '../../../components';

import AddDriverModel from './AddDriverModel';
import DriverCommissionModel from './DriverCommissionModel';

import {
  // eslint-disable-next-line no-unused-vars
  getAllChefSaga,
  // eslint-disable-next-line no-unused-vars
  resetChefList,
  editChefFeatureSaga,
  getAllDriverSaga,
  // eslint-disable-next-line no-unused-vars
  resetDriverList,
  editBlockDriverStatusSaga,
} from '../../../store/actions';

// eslint-disable-next-line no-unused-vars
const Drivers = props => {
  // eslint-disable-next-line no-unused-vars
  const [status, setStatus] = useState('approved');
  const [mounted, setMounted] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [currentRecord, setCurrentRecord] = useState(null);
  const [commissionModelOpen, setCommissionModelOpen] = useState(false);
  const [tableFilter, setTableFilter] = useState({});
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);

  // Add Driver Modal
  const [modelOpen, setModelOpen] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [isEdit, setEditCategory] = useState(false);

  const setTableFilters = (columnName, orderBy, search, skip, limit) => {
    tableFilter.columnName = columnName;
    tableFilter.orderBy = orderBy;
    tableFilter.search = search;
    tableFilter.skip = skip;
    tableFilter.limit = limit;
    setTableFilter(tableFilter);
  };

  const dispatch = useDispatch();

  const {
    drivers: { list: allData, totalRecords },
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.driver);
  // eslint-disable-next-line no-unused-vars
  const { open } = useSelector(state => state.modal);

  const getData = (columnName, orderBy, search, skip, limit) => {
    setTableFilters(columnName, orderBy, search, skip, limit);
    if (search && skip !== 0) {
      dispatch(
        getAllDriverSaga({
          columnName,
          orderBy,
          search,
          skip: 0,
          limit,
          status,
        }),
      );
    } else {
      dispatch(
        getAllDriverSaga({ columnName, orderBy, search, skip, limit, status }),
      );
    }
  };

  useEffect(() => {
    setMounted(true);
    getData('name', 'asc', '', 0, 10);
    return () => setMounted(false);
  }, []);

  useEffect(() => {
    if (mounted) {
      getData();
    }
  }, [status]);

  const statusAction = data => {
    dispatch(editBlockDriverStatusSaga({ id: data.id, isBlock: data.status }));
  };

  const statusActionChefFeature = data => {
    dispatch(editChefFeatureSaga({ id: data.id, isFeatured: data.status }));
  };

  const tableFields = [
    { displayName: 'Driver ID', key: 'licenseNumber' },

    {
      displayName: 'Profile',
      key: 'profile',
      noSort: true,
      isMedia: true,
      mediaType: 'image',
    },
    {
      displayName: 'Name',
      key: 'name',
      noSort: true,
    },
    {
      displayName: 'Phone',
      key: 'phone',
    },
    {
      displayName: 'Trips',
      key: 'trips',
    },
    {
      displayName: 'Ride Income',
      key: 'rideIncome',
    },
    {
      displayName: 'Status',
      key: 'isBlock',
      noSort: true,
      isStatus: true,
      positiveValue: false,
      action: statusAction,
    },
    // {
    //   displayName: 'Feature',
    //   key: 'isFeatured',
    //   noSort: true,
    //   isStatus: true,
    //   positiveValue: true,
    //   action: statusActionChefFeature,
    // },
    {
      displayName: 'Driver Rating',
      key: 'rating',
      className: 'active-green',
      isRating: true,
      ratingType: 'star',
      redirectLink: '/vendors/chefs/rating-detail',
    },
    {
      displayName: 'Registered Date',
      key: 'createdAt',
      isDate: true,
    },
    {
      displayName: 'Approved',
      key: 'isApproved',
      action: statusActionChefFeature,
    },
    // {
    //   displayName: 'Commission(%)',
    //   key: 'commissionPercentage',
    //   isActions: true,
    //   actions: [
    //     {
    //       view: record => <span>{record.commissionPercentage || 0}</span>,
    //       action: record => {
    //         setCommissionModelOpen(true);
    //         setCurrentRecord(record);
    //       },
    //     },
    //   ],
    // },
    {
      displayName: 'Actions',
      noSort: true,
      isActions: true,
      actions: [
        {
          view: <i className="fa fa-eye" aria-hidden="true" />,
          action: '/promoters/drivers/driver-detail',
        },
      ],
    },
  ];

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>LIST OF DRIVERS </h2>
              </div>
              {/* Temporary Add Driver Model for API Testing */}
              <div className="text-right  d-flex-right">
                <Link
                  to="/promoters/drivers/add-driver"
                  className="btn btn-primary m-l-sm m-b-md"
                >
                  Add Driver
                </Link>
                {/* <button
                  type="submit"
                  className="btn btn-primary m-l-sm m-b-md"
                  data-toggle="modal"
                  data-target="#send-modal"
                  onClick={() => {
                    setEditCategory(false);
                    setModelOpen(true);
                  }}
                >
                  Add Driver
                </button> */}
              </div>
              <div className="coman-table" id="owner-detail-btn2">
                <Table
                  headers={tableFields}
                  tableData={allData || null}
                  pageCount={totalRecords?.count}
                  getData={getData}
                  // link={linkObj}
                  linkPropsName="_id"
                  // statusAction={statusAction}
                  setSearchFlag={searchFlagSetter}
                  errorMsg={errorMsg || 'Loading...'}
                  // pageSizeProp={pageSize}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      {modelOpen && (
        <AddDriverModel
          setModelOpen={setModelOpen}
          tableFilter={tableFilter}
          isEdit={isEdit}
        />
      )}
      {commissionModelOpen && (
        <DriverCommissionModel
          setModelOpen={setCommissionModelOpen}
          chefId={currentRecord._id}
          isUpdated
          commissionPercentage={currentRecord.commissionPercentage}
          tableFilter={tableFilter}
        />
      )}
    </div>
  );
};

export default SetTokenHeader(Drivers, axios);
